import React, { useEffect, useState } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';
import './ProductsPage.scss';
import BreadCrumbs from '../BreadCrumbs/BreadCrumbs';
import Selectors from '../Selectors/Selectors';
import ProductPage from '../homePage/ProductPage/ProductPage';
import { getProducts } from '../../helpers/api';
import GadgetCard from '../homePage/GadgetCard/GadgetCard';

type Param = {
  item: string | undefined;
};

const ProductsPage = () => {
  const params: Param = useParams();
  const location = useLocation();
  const { pathname } = location;
  const [preparedGadgets, setPreparedGadgets] = useState<Gadget[]>([]);

  useEffect(() => {
    getProducts().then(data => setPreparedGadgets(data.filter((gadget: Gadget) => (
      gadget.type === pathname.replace('/', '')
    ))));
  }, [pathname]);
  console.log(preparedGadgets);
  console.log(pathname.replace('/', ''));

  return (
    <>
      { params.item
        ? <ProductPage currentProduct={params.item} />
        : (
          <div className="productPage">
            <BreadCrumbs />
            {pathname.replace('/', '') === 'phone'
              ? (
                <div>
                  Mobile phone
                </div>
              )
              : pathname.replace('/', '') === 'tablet'
                ? (
                  <div>
                    Tablets
                  </div>
                )
                : (
                  <div>
                    Accessories
                  </div>
                )}
            <Selectors />
            <div
              className="productsPage__card"
            >
              {preparedGadgets.map(gadget => (

                <GadgetCard
                  gadget={gadget}
                  key={uuidv4()}
                />

              ))}
            </div>
          </div>
        )}
    </>
  );
};

export default ProductsPage;
